import React, {Component} from 'react';
import { TextField, List, ListItem, ListItemText } from "@material-ui/core";
import firebase from "./../firebase";
//import $ from 'jquery'; './jquery.dataTables.min.css';
import ReactJWPlayer from 'react-jw-player';
// eslint-disable-next-line import/no-webpack-loader-syntax
//import Captions from "file-loader!../assets/sub/Avatar_2009_sub_ro.vtt";
import $ from 'jquery';
import moment from 'moment';

//const $ = require('jquery');
$.DataTable = require('datatables.net');

export default class Bodycontenttvjw extends Component {
    constructor(props) {
        super(props);
        this.state = {
            user: [],
            role: '',
            count:0,
            channels: [],
            all_channels: [],
            datatable: [],
            addsopcast: 'sop://broker.sopcast.com:3912/6002',
            addtitlesopcast: 'CCV5',
            playlist: 'https://stream.adunity.com/b1/b1_640-.m3u8',
            videoTitle: '',
            avatar_sub:false,
            avatar_movie:false,
            gladiator_movie:false,
            autostart:true,
            jwplayer:true,
            videoplayer:false,
            videoadultplayer:false,
            jwtcategory:[
              {
                'id': 1,
                'title': 'TV Channels',
                'description': 'Description of TV Channels'
              },
              {
                'id': 2,
                'title': 'Adult TV Channels',
                'description': 'Description of Adult TV Channels'
              },
              {
                'id': 3,
                'title': 'Movies Channels',
                'description': 'Description of Movies Channels'
              },
              {
                'id': 4,
                'title': 'Adult Movie Channels',
                'description': 'Description of Adult Movie Channels'
              },
            ],
            countjwtcategory:0,
            jwtcategories:''
        };
    
        this.onAdPlay = this.onAdPlay.bind(this);
        this.onReady = this.onReady.bind(this);
        this.onVideoLoad = this.onVideoLoad.bind(this);
    
        // each instance of <ReactJWPlayer> needs a unique id.
        // we randomly generate it here and assign to the container instance.
        this.playerId = '123456';
    }

    onReady(event) {
      // interact with JW Player API here
      const player = window.jwplayer(this.playerId);
    }
    onAdPlay(event) {
      // track the ad play here
    }
    onVideoLoad(event) {
      this.setState({
        videoTitle: event.item.description // this only works with json feeds!
      });
    }

    componentDidMount() {
      //alert(localStorage.getItem('user_id'))
      this.getUser()
      this.setCategory_jwt_category()

      fetch('https://newuniverses.go.ro/slim430/channelstv/all')
        .then(res => res.json())
        .then((data) => {
          //alert(JSON.stringify(data))
          if(data.length>1){
            this.setState({ channels: data })
            firebase.database().ref("/channelstv_jwt").remove()
            data.forEach(child => {
            var refdb = firebase.database().ref('/channelstv_jwt').push();
            refdb = refdb.toString().slice(-20);
            let updateData = firebase.database().ref("/channelstv_jwt").child(refdb);
            updateData.child("id").set(child.id);
            updateData.child("denumire").set(child.denumire);
            updateData.child("adresa").set(child.adresa);
            updateData.child("category").set(child.category);
            })
          }else{
            firebase.database().ref("/channelstv_jwt").on('value', snapshot => {
              var channelstv_jwt = []
              snapshot.forEach(child => {
                var data = child.val()
                  channelstv_jwt.push({
                    id: data.id,
                    denumire: data.denumire,
                    adresa: data.adresa,
                    category: data.category
                  })
              this.setState({ channels: channelstv_jwt, all_channels: channelstv_jwt })
            })
          })
          }
          this.setState({
            all_channels: this.state.channels
          });
          setTimeout(function(){ 
          if(this.state.role === '"adminc"'){
            //alert('Adminc');
            //alert(this.state.role);
            var channelstv_jwt = [] 
            this.state.channels.forEach(data => {
              //alert(data.category);
              if((parseInt(data.category) !== 2) && (parseInt(data.category) !== 4)){
              //  alert(data.category);
              channelstv_jwt.push({
                id: data.id,
                denumire: data.denumire,
                adresa: data.adresa,
                category: data.category
              })
            }
          })
          this.setState({ channels: channelstv_jwt, all_channels: channelstv_jwt })

          var cat_channelstv_jwt = [] 
            this.state.jwtcategory.forEach(data => {
              //alert(data.category);
              if((parseInt(data.id) !== 2) && (parseInt(data.id) !== 4)){
              //  alert(data.category);
              cat_channelstv_jwt.push({
                id: data.id,
                title: data.title,
                description: data.description
              })
            }
          })
          this.setState({ jwtcategory: cat_channelstv_jwt })
          }
        }.bind(this), 400); 
        })
        .catch((error)=>{
          firebase.database().ref("/channelstv_jwt").on('value', snapshot => {
            var channelstv_jwt = []
            snapshot.forEach(child => {
              var data = child.val()
              //alert(parseInt(data.category));
                channelstv_jwt.push({
                  id: data.id,
                  denumire: data.denumire,
                  adresa: data.adresa,
                  category: data.category
                })
            this.setState({ channels: channelstv_jwt, all_channels: channelstv_jwt })
          })
        });
        this.setState({
          all_channels: this.state.channels
        });
        setTimeout(function(){ 
          if(this.state.role === '"adminc"'){
            //alert('Adminc');
            //alert(this.state.role);
            var channelstv_jwt = [] 
            this.state.channels.forEach(data => {
              if((parseInt(data.category) !== 2) && (parseInt(data.category) !== 4)){
              channelstv_jwt.push({
                id: data.id,
                denumire: data.denumire,
                adresa: data.adresa,
                category: data.category
              })
            }
          })
          this.setState({ channels: channelstv_jwt, all_channels: channelstv_jwt })
          
          var cat_channelstv_jwt = [] 
            this.state.jwtcategory.forEach(data => {
              //alert(data.category);
              if((parseInt(data.id) !== 2) && (parseInt(data.id) !== 4)){
              //  alert(data.category);
              cat_channelstv_jwt.push({
                id: data.id,
                title: data.title,
                description: data.description
              })
            }
          })
          this.setState({ jwtcategory: cat_channelstv_jwt })
          }
        }.bind(this), 400); 
        console.log(error)
      });
    }

    //componentWillUnmount(){
    //  this.$el.DataTable.destroy(true);
    //}

    getUser = () => {
      
      firebase.database().ref("/users").orderByChild('email')
      .equalTo(localStorage.getItem('user_id')).on('value', snapshot => {
        var userg = []
        snapshot.forEach(child => {
          var data = child.val()
        userg.push({
          id: data.key, 
          email: data.email,
          name: data.name,
          nickname: data.nickname,
          phone: data.phone,
          role: data.role,
          time: data.time
        })
        this.setState({ user: userg })
      })
        
      })
      setTimeout(function(){ 
        this.setState({ role: JSON.stringify(this.state.user[0].role) })
        //alert(JSON.stringify(this.state.role))
      //alert(JSON.stringify(this.state.user[0].role))   
      }.bind(this), 300);   
    }

    tocar = () =>{

      var streamingUrl = document.getElementById('txUrl').value;
  
      console.log(streamingUrl);
  
      window.jwplayer('mediaspace').setup({
        //'flashplayer': 'jwplayer/jwplayer.flash.swf',       
        //'file': 'rtmp://37.26.69.49:80/live/syri',
        'file': streamingUrl,
        //'file': 'http://192.168.43.1:8080/spydroid.sdp?id=0&h264=500-20-320-240&flash=off&camera=0',
        'autostart': 'true',
        'primary': 'flash',
        'width': '470',
        'height': '320',
        'mediaid': '123456'
      });
      window.jwplayer.key="[HereWouldBeMyKey]";
    }

    setSelectedOption = (val) =>{
      //alert(JSON.stringify(val));
      this.setState({
        playlist: val
      });
      setTimeout(function(){
      //alert(this.state.playlist.slice(-21));
      if(this.state.playlist.slice(-17)==='filename=name.mp4'){
        this.setState({
          jwplayer: false,
          videoplayer: true
        });
      }
      if(this.state.playlist.slice(-17)!=='filename=name.mp4'){
        this.setState({
          jwplayer: true,
          videoplayer: false
        });
      }
      if(this.state.playlist.slice(-18)==='filename=adult.mp4'){
        //var urllenght=this.state.playlist.length-19;
        //alert(this.state.playlist.slice(0,urllenght));
        //window.jwplayer(this.playerId).setup({
        //  'file': this.state.playlist.slice(0,urllenght),
        //});
        /*
        var agent = navigator.userAgent;
        var isIphone = ((agent.indexOf('iPhone') != -1) || (agent.indexOf('iPod') != -1)) ;

        $videoTag = "";
        if(isIphone()) {
            $videoTag = '<video controls autoplay preload="metadata">';
        } else {
            $videoTag = '<video controls preload="metadata">';
        }
        */
        this.setState({
          jwplayer: false,
          videoadultplayer: true
        });
      }

      if(this.state.playlist.slice(-18)!=='filename=adult.mp4'){
        this.setState({
          jwplayer: true,
          videoadultplayer: false
        });
      }

      if(this.state.playlist.slice(-21)==='filename=avatarul.mp4'){
        this.setState({
          jwplayer: false,
          avatar_movie: true
        });
      }
      if(this.state.playlist.slice(-21)!=='filename=avatarul.mp4'){
        this.setState({
          jwplayer: true,
          avatar_movie: false
        });
      }
      
      if(this.state.playlist.slice(-24)==='filename=gladiatorul.mp4'){
        this.setState({
          jwplayer: false,
          gladiator_movie: true
        });
      }
      if(this.state.playlist.slice(-24)!=='filename=gladiatorul.mp4'){
        this.setState({
          jwplayer: true,
          gladiator_movie: false
        });
      }

    }.bind(this), 500);
    }

    setSelectedOptionjwtcategory = (val) =>{
      this.setState({
        jwtcategories: val
      });
      setTimeout(function(){
     //alert(JSON.stringify(val));
     //alert(JSON.stringify(this.state.all_channels));
     var channelstv_jwt = [] 
      this.state.all_channels.forEach(data => {
        if(data.category === val){
        channelstv_jwt.push({
          id: data.id,
          denumire: data.denumire,
          adresa: data.adresa,
          category: data.category
        })
      }
    })
    if(parseInt(val) === 0){
      channelstv_jwt = this.state.all_channels
    }
    this.setState({ channels: channelstv_jwt })
    }.bind(this), 300);
    }

    setCategory_jwt_category = () =>{
      var curtime = moment().format("DD/MM/YYYY HH:mm:ss");

      firebase.database().ref("/channels_jwt_category").remove()
      this.state.jwtcategory.forEach(child => {
      var refdb = firebase.database().ref('/channels_jwt_category').push();
      refdb = refdb.toString().slice(-20);
      let updateData = firebase.database().ref("/channels_jwt_category").child(refdb);
      updateData.child("id").set(child.id);
      updateData.child("title").set(child.title);
      updateData.child("description").set(child.description);
      updateData.child("time").set(curtime);
      });
    }

    render(){
          const divStyless={  
            height:'150px',
            position:'relative',
            marginBottom: '350px'
          };
          const divStylesssp={
            position:'relative',
            marginBottom: '250px',
            diplay:'flex'
          };
          const videoplayerstyle={
            marginBottom: '20px',
            width: '590px'
          }
        return (
            <main role="main" className="col-md-9 ml-sm-auto col-lg-10 pt-3 px-4">
            <div className="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pb-2 mb-3 border-bottom">
              <h1 className="h2">Dashboard TVJW Page</h1>
            </div>
            <p>Dashboard TVJW Page</p>

            <div style={divStyless}>

            {this.state.avatar_movie!==true && this.state.jwplayer===true && (<ReactJWPlayer
              playlist={[{
              file: this.state.playlist,
              tracks: [{
                file: this.state.playlist.slice(-10)==='avatar.mp4'?'https://vasileclaudiu.github.io/reactsopcast/assets/sub/Avatar_2009_sub_ro.vtt':(this.state.playlist.slice(-13)==='gladiator.mp4'?'https://vasileclaudiu.github.io/reactsopcast/assets/sub/Gladiator_2000_sub_ro.vtt':''),
                label: 'Romanian',
                kind: 'captions',
                'default': true
              }],
              }]}
              isAutoPlay={this.state.autostart}
              playerId={this.playerId} // bring in the randomly generated playerId
              playerScript="https://vasileclaudiu.github.io/reactsopcast/assets/js/jwplayer-8.11.3.js"
            />)}
            {this.state.videoplayer===true && (<video id="my-video-stream" controls="controls" preload="metadata" style={videoplayerstyle} src={this.state.playlist} autoPlay>
                <source src="vid/vid.mp4" type="video/mp4"/>
                <track label="Romanian" kind="subtitles" src="assets/sub/Avatar_2009_sub_ro.vtt" srcLang="ro" default />
                </video>)}
            {this.state.videoplayer===true && (<br/>)}
            {this.state.avatar_movie===true && (<video id="my-video-stream" controls="controls" preload="metadata" style={videoplayerstyle} src={this.state.playlist} autoPlay>
                <source src="vid/vid.mp4" type="video/mp4"/>
                <track label="Romanian" kind="subtitles" src="assets/sub/Avatar_2009_sub_ro.vtt" srcLang="ro" default />
                </video>)}
            {this.state.avatar_movie===true && (<br/>)}
            {this.state.gladiator_movie===true && (<video id="my-video-stream" controls="controls" preload="metadata" style={videoplayerstyle} src={this.state.playlist} autoPlay>
                <source src="vid/vid.mp4" type="video/mp4"/>
                <track label="Romanian" kind="subtitles" src="assets/sub/Gladiator_2000_sub_ro.vtt" srcLang="ro" default />
                </video>)}
            {this.state.gladiator_movie===true && (<br/>)}
            {this.state.videoadultplayer===true && (<video id="my-video-stream" controls="controls" preload="auto" style={videoplayerstyle} src={this.state.playlist.slice(0,this.state.playlist.length-19)} autoPlay>
                <source src="vid/vid.mp4" type="video/mp4"/>
                </video>)}
            {this.state.videoadultplayer===true && (<br/>)}
<div className="row"> 
<label>Selectati categoria:</label>

<select id="txUrl" className="custom-select" value={this.state.jwtcategories} onChange={e => this.setSelectedOptionjwtcategory(e.target.value)}>
    <option>Selectati categoria</option>
    {this.state.jwtcategory.map((channel) => (
    <option value={channel.id}>{channel.title}</option>
    ))}
    <option value="0">Toate</option>
</select><br/>
<label>Selectati sursa:</label>

<select id="txUrl" className="custom-select" value={this.state.playlist} onChange={e => this.setSelectedOption(e.target.value)}>
    <option>Selectati sursa</option>
    {this.state.channels.map((channel) => (
    <option value={channel.adresa}>{channel.denumire}</option>
    ))}
</select><br/>
<label for="inputman">Input Manual</label>
<input id="inputman" type="text" className="form-control" value={this.state.playlist} onChange={e => this.setSelectedOption(e.target.value)}></input>
<hr/>
</div>
            </div>
          </main>  
        )
    }
}